@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(AvenirNext-Regular, AvenirNext-Regular);
@mixin f {
  font-family: 'AvenirNext-Regular', serif; }
@include font(AvenirNext-DemiBold, AvenirNext-DemiBold);
@mixin fb {
  font-family: 'AvenirNext-DemiBold', serif; }

$cl: #2C3235;
$border: rgba($cl,.32);
$brand: #609BB7;
$green: #44B85A;

@mixin lh {
  line-height: (24/15); }
@mixin bs {
  box-shadow: 0 4px 16px rgba(black,.04); }
@mixin scroll {
  &::-webkit-scrollbar-track {
    background: white; }
  &::-webkit-scrollbar {
    width: 2px;
    background: white; }
  &::-webkit-scrollbar-thumb {
    background: rgba($cl,.16);
    border-radius: 2px; } }

body {
  background: $cl;
  @include fb;
  font-size: 15px;
  line-height: 1.5;
  letter-spacing: .2px;
  &.no-scroll {
    overflow: hidden; } }
button,
input,
textarea,
select {
  font-size: 15px; }
input,
textarea,
select {
  @include f; }
button,
strong,
b {
  @include fb; }
button {
  background: none; }
a {
  text-decoration: none; }

.body {
  margin-bottom: 40px; }

.text-center {
  text-align: center; }

.center {
  max-width: 1332px;
  margin: 0 auto;
  padding: 0 50px;
  @include m {
     padding: 0 15px; }
  &_sm {
    max-width: 772px; } }

.header {
  margin-bottom: 72px;
  padding-top: 16px;
  &__center {
    display: flex; }
  &__user {
    margin-left: auto; } }

.user {
  position: relative;
  &__head {
    display: flex;
    height: 40px;
    padding: 8px 9px 8px 8px;
    background: rgba(black,.04);
    border: 1px solid rgba(black,.08);
    align-items: center;
    user-select: none;
    cursor: pointer; }
  &__preview {
    flex: 0 0 24px; }
  &__pic {
    display: block;
    max-width: 24px;
    border-radius: 2px; }
  &__name {
    margin: 0 5px 0 8px;
    font-size: 16px;
    color: white; }
  &__arrow {
    font-size: 0;
    .icon {
      font-size: 5px;
      fill: rgba(white,.64); } }
  &__drop {
    display: none;
    position: absolute;
    top: 36px;
    right: -8px;
    width: 192px;
    padding: 16px;
    background: white;
    border-radius: 4px;
    @include bs; }
  &__link {
    display: block;
    color: rgba(black,.64);
    @include lh; }
  &.visible &__arrow {
    .icon {
      transform: rotate(180deg); } }
  &.visible &__drop {
    display: block; } }

.title {
  margin-bottom: 32px;
  @include f;
  font-size: 32px;
  &_3 {
    margin-bottom: 16px;
    @include fb;
    font-size: 16px; } }

.switch {
  display: block;
  position: relative;
  user-select: none;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__wrap &__icon {
    .icon {
      opacity: 1; } }
  &__wrap {
    display: flex; }
  &__icon {
    height: 16px;
    margin: 3px 8px 0 0;
    padding: 4px 2px;
    background: rgba($brand,.16);
    border-radius: 2px;
    flex: 0 0 16px;
    .icon {
      display: block;
      font-size: 8px;
      opacity: 0;
      fill: $brand;
      transition: opacity .2s; } }
  &__content {
    @include f;
    @include lh; } }

.toggle {
  &__icon,
  &__title {
    display: inline-block;
    vertical-align: middle; }
  &__icon {
    position: relative;
    width: 16px;
    height: 16px;
    margin-right: 8px;
    border-radius: 2px;
    background: rgba($cl,.08); }
  &__title {
    position: relative;
    top: 1px;
    @include lh; }
  .icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    opacity: .64;
    fill: black;
    &-plus {
      font-size: 8px; }
    &-minus {
      font-size: 2px; } } }

.field {
  position: relative;
  margin-bottom: 16px;
  &__wrap {
    position: relative; }
  &__arrow {
    position: absolute;
    top: 21px;
    right: 17px;
    font-size: 0;
    pointer-events: none;
    .icon {
      font-size: 7px;
      fill: rgba($cl,.64); } }
  &__arrow + &__input {
    padding-right: 30px; }
  &__input {
    width: 100%;
    height: 48px;
    padding: 0 12px;
    border-radius: 4px;
    border: 1px solid $border;
    transition: border-color .2s;
    &:hover {
      border-color: $brand; } }
  &__input:hover + &__label {
    opacity: .64; }
  &__input:focus + &__label,
  &__input.ng-touched.ng-not-empty + &__label,
  &__input.focus + &__label {
    top: -5px;
    opacity: 1;
    @include fb;
    font-size: 11px;
    text-transform: uppercase;
    color: $brand;
    letter-spacing: .8px; }
  &__label {
    position: absolute;
    top: 13px;
    left: 9px;
    max-width: 100%;
    padding: 0 4px;
    background: white;
    opacity: .32;
    @include f;
    pointer-events: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    transition: all .2s; }
  &__drop {
    display: none;
    position: absolute;
    top: 40px;
    left: -4px;
    right: 24px;
    padding: 4px;
    border-radius: 4px;
    background: white;
    @include bs;
    z-index: 50; }
  &__list {
    max-height: 112px;
    padding: 12px 0;
    overflow: auto;
    @include scroll; }
  &__option {
    padding: 0 12px;
    color: #BBBDBE;
    cursor: pointer;
    @include lh;
    @include nl {
      margin-bottom: 8px; } }
  &__query {
    color: #787C7E; } }

.btn {
  padding: 13px 23px 14px;
  background: $brand;
  border-radius: 4px;
  border: 1px solid transparent;
  font-size: 14px;
  color: white;
  letter-spacing: .8px;
  text-transform: uppercase;
  transition: background .2s, border-color .2s;
  &:hover {
    background: #6ca2bc;
    border-color: #588ea8; }
  &:active {
    background: #508299;
    border-color: #508299; } }
a.btn {
  display: inline-block; }

.btn-def {
  color: rgba(black,.64); }

.row {
  display: flex;
  margin: 0 -8px;
  @include m {
    display: block; }
  &__col {
    padding: 0 8px;
    flex: 1 0 auto; } }

.block {
  margin-bottom: 16px;
  padding: 32px;
  background: white;
  border-radius: 4px;
  @include bs;
  @include m {
    padding: 16px; }
  &:hover {
    box-shadow: 0 2px 8px rgba(black,.02); } }

.form {
  &__switch {
    margin-top: 16px; }
  &__foot {
    margin-top: 32px;
    text-align: center; }
  &__btn {
    margin: 0 8px; } }

.hotline {
  @include f;
  font-size: 12px;
  color: white;
  text-align: center;
  line-height: 1.333;
  a {
    @include fb;
    color: #B2B5B6; } }

.text {
  margin-bottom: 32px;
  @include f;
  @include lh;
  p {
    margin-bottom: 24px; }
  strong,
  b {
    @include fb; } }

.scan {
  margin-bottom: 16px;
  &__preview {
    // min-height: 160px
    margin-bottom: 16px;
 }    // background: url(../img/placeholder.png) 0 0 / 24px 24px
  &__pic {
    display: block;
    max-width: 100%; } }

.nodes {
  margin-bottom: 32px;
  &__row {
    display: flex;
    padding: 16px 0;
    border-bottom: 1px solid rgba($cl,.04);
    align-items: center;
    &:first-child {
      padding-top: 0; } }
  &__item {
    display: flex;
    margin-right: 20px;
    align-items: center; }
  &__status {
    width: 16px;
    height: 16px;
    margin-right: 8px;
    padding: 5px 0 0 4px;
    border-radius: 50%;
    background: rgba($green,.16);
    .icon {
      display: block;
      font-size: 6px;
      fill: $green; } }
  &__title {
    position: relative;
    top: 1px; }
  &__btn {
    margin-left: auto; } }

.finish {
  margin: -16px 0 32px;
  &__text {
    margin-bottom: 16px; }
  &__nodes {
    margin-bottom: 32px;
    color: #3d6375;
    text-align: center;
    @include lh; } }
